import { BrowserModule } from '@angular/platform-browser';
import { HttpModule } from '@angular/http';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { InAppBrowser } from '@ionic-native/in-app-browser';
import { MomentModule } from 'angular2-moment';

import { MyApp } from './app.component';
import { SharedModule } from './shared.module';
import { GreybackProvider } from '../providers/greyback/greyback';
import { AudioProvider } from '../providers/audio/audio';
import { HttpClientModule } from '@angular/common/http';

import { Pro } from '@ionic/pro';
const IonicPro = Pro.init('2191a4b0', {
	appVersion: "1.0.0"
});
export class MyErrorHandler implements ErrorHandler {
	handleError(err: any): void {
		IonicPro.monitoring.handleNewError(err);
	}
}
@NgModule({
	declarations: [
		MyApp
	],
	imports: [
		BrowserModule,
		HttpModule,
		IonicModule.forRoot(MyApp, {
			mode: 'ios'
		}, 
{
  links: [
    { loadChildren: '../pages/about/about.module.ngfactory#AboutPageModuleNgFactory', name: 'AboutPage', segment: 'about', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/article-detail/article-detail.module.ngfactory#ArticleDetailPageModuleNgFactory', name: 'ArticleDetailPage', segment: 'article/:articleId', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/credits/credits.module.ngfactory#CreditsPageModuleNgFactory', name: 'CreditsPage', segment: 'credits', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/event-detail/event-detail.module.ngfactory#EventDetailPageModuleNgFactory', name: 'EventDetailPage', segment: 'event/:eventId', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/events/events.module.ngfactory#EventsPageModuleNgFactory', name: 'EventsPage', segment: 'events', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/jesus/jesus.module.ngfactory#JesusPageModuleNgFactory', name: 'JesusPage', segment: 'jesus', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/livestream/livestream.module.ngfactory#LivestreamPageModuleNgFactory', name: 'LivestreamPage', segment: 'livestream', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/location/location.module.ngfactory#LocationPageModuleNgFactory', name: 'LocationPage', segment: 'location', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/menu/menu.module.ngfactory#MenuPageModuleNgFactory', name: 'MenuPage', segment: 'menu', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/post-detail/post-detail.module.ngfactory#PostDetailPageModuleNgFactory', name: 'PostDetailPage', segment: 'post/:postId', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/prayer/prayer.module.ngfactory#PrayerPageModuleNgFactory', name: 'PrayerPage', segment: 'prayer', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/resources/resources.module.ngfactory#ResourcesPageModuleNgFactory', name: 'ResourcesPage', segment: 'resources', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/series/series.module.ngfactory#SeriesPageModuleNgFactory', name: 'SeriesPage', segment: 'series', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sermon/sermon.module.ngfactory#SermonPageModuleNgFactory', name: 'SermonPage', segment: 'sermon', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sermons/sermons.module.ngfactory#SermonsPageModuleNgFactory', name: 'SermonsPage', segment: 'sermons/:seriesId', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/stories/stories.module.ngfactory#StoriesPageModuleNgFactory', name: 'StoriesPage', segment: 'stories', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/staff/staff.module.ngfactory#StaffPageModuleNgFactory', name: 'StaffPage', segment: 'staff', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module.ngfactory#TabsPageModuleNgFactory', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/times/times.module.ngfactory#TimesPageModuleNgFactory', name: 'TimesPage', segment: 'times', priority: 'low', defaultHistory: [] }
  ]
}),
		MomentModule,
		SharedModule,
		HttpClientModule
	],
	exports: [],
	bootstrap: [IonicApp],
	entryComponents: [
		MyApp
	],
	providers: [
		StatusBar,
		SplashScreen,
		{ provide: ErrorHandler, useClass: IonicErrorHandler },
		GreybackProvider,
		AudioProvider,
		InAppBrowser,
		SharedModule
	]
})
export class AppModule { }



